.how-working {
  position: relative;
  min-height: 575px;
  background: url("../img/main-banner.png") 0 0/cover no-repeat;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

.how-working::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 168, 0, 0.3);
  z-index: 2;
}

.how-working__container {
  position: relative;
  max-width: 1408px;
  margin: 0 auto;
}

.how-working__title {
  position: relative;
  z-index: 3;
  color: #fff;
  padding: 74px 0 0 0;
}

.how-working__content {
  position: relative;
  z-index: 3;
  display: flex;
  justify-content: space-between;
  max-width: 1090px;
  margin: 106px auto 0;
}

.how-working__content .how-working__card {
  width: 33.333%;
  color: #E5E5E5;
  text-align: center;
  font-size: 24px;
  font-weight: 400;
  line-height: 28.13px;
}

.how-working__content .how-working__card-img {
  margin-bottom: 36px;
}

.how-working__content .how-working__card-img img {
  border: 15px solid rgba(229, 229, 229, 0.2);
  border-radius: 50%;
}

.how-working__content .how-working__card-text {
  max-width: 357px;
}

.how-working__button {
  position: absolute;
  top: 60px;
  right: 8px;
  z-index: 4;
  padding: 15px 77px;
  color: #FFF;
  font-weight: 700;
  font-size: 24px;
  background-color: rgba(255, 168, 0, 0.3);
  border: 1px solid #fff;
  border-radius: 5px;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}
