.header {
  min-height: 970px;
  background: url("../img/header-background.png") 0 0/cover no-repeat;
}

.header .header__container {
  max-width: 1400px;
  margin: 0 auto;
}

.header__content {
  max-width: 1215px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  padding: 347px 0 0 0;
}

.header__content .header__content-title {
  font-size: 72px;
  font-weight: 100;
  color: #FFF;
  line-height: 84px;
  padding: 0 162px 0 0;
  align-self: center;
}

.header__content .header__content-title span {
  font-weight: 700;
}

.header-line {
  height: 8px;
  background-color: #FFA800;
}
