.form-router {
  background: rgba(41, 41, 41, 0.8);
  padding: 22px;
  width: 729px;
}

.form-router .select-grup {
  display: flex;
  justify-content: space-between;
}

.form-router label {
  display: block;
  color: #FFF;
  font-size: 30px;
  padding: 60px 0 15px 6px;
}

.form-router .select-router {
  width: 325px;
  height: 60px;
  border-radius: 3px;
}

.form-router .select-router .placehold {
  font-weight: 400;
  font-size: 18px;
  color: #E5E5E5;
}

option:first {
  color: #E5E5E5;
  font-weight: 400;
  font-size: 18px;
}

.cashed {
  align-self: center;
  margin: 0 5px;
}

.button {
  width: 323px;
  margin: 101px 0 30px auto;
}

.button-router {
  display: block;
  position: relative;
  margin-left: auto;
  text-transform: uppercase;
  font-size: 24px;
  padding: 17px 68px;
  background-color: #FFA800;
  color: #292929;
  font-weight: 700;
  border-radius: 5px;
}

.header-line {
  height: 8px;
  background-color: #FFA800;
}
