.choice {
  width: 959px;
  height: 354px;
  background: #FFFFFF;
  border: 1px solid #C4C4C4;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
  display: flex;
  margin: 0 auto;
  margin-bottom: 47px;
}

.choice_container {
  display: flex;
}

.choice_icon {
  align-items: center;
  margin: 0px 0px 17px 0px;
}

.choice_number {
  font-size: 24px;
  color: #3E3C41;
  line-height: 116%;
  font-weight: 700;
  margin: 0px 0px 15px 10px;
}

.choice_menu.menu_choice {
  margin: 0 0 0 -18px;
}

.choice_menu_icon {
  display: flex;
}

.choice_city_transparent {
  color: #928F94;
  font-size: 16px;
  margin: 0 0 0 0;
}

.choice_city {
  font-size: 16px;
  color: #292929;
  margin: 2px 0 4px 0;
}

.choice_column_a_train_container {
  display: flex;
  flex-direction: column;
  background: #E4E0E9;
  width: 197px;
  box-sizing: content-box;
  height: 353px;
}

.choice_column_a_train {
  margin: 58px auto 209px auto;
  align-items: center;
}

.choice_main_group {
  display: flex;
  flex-direction: column;
}

.choice_time_container {
  display: flex;
  justify-content: space-between;
  margin: 54px 36px 66px 39px;
}

.choice_time_container_footer {
  display: flex;
  margin: 54px 36px 83px 39px;
}

.choice_time {
  margin: 0px 0px 0px 0px;
}

.choice_time2 {
  margin: 0px 0px 0px 20px;
}

.choice_time-start {
  font-size: 24px;
  font-weight: 700;
  line-height: 116%;
  color: #000000;
}

.travel_time {
  font-size: 18px;
  line-height: 116%;
}

.time::after {
  content: '';
  position: absolute;
  top: 23px;
  left: 3px;
  background: url("../img/Vector orange.svg") 0 0 no-repeat;
  width: 30px;
  height: 20px;
}

.time2::after {
  content: '';
  position: absolute;
  top: 30px;
  left: 3px;
  background: url("../img/Vector orange return.svg") 0 0 no-repeat;
  width: 30px;
  height: 20px;
}

.choice_travel_start {
  position: relative;
  margin: 0px 0px 0px 36px;
}

.choice_price_container {
  border: 1px dashed #E5E5E5;
  width: 290px;
}

.choice_price_group {
  display: flex;
  align-items: center;
  justify-content: center;
}

.choice_seat {
  color: #3E3C41;
  font-size: 16px;
  font-weight: 400;
  line-height: 117%;
}

.choice_seat_quantity {
  font-size: 16px;
  font-weight: 500;
  line-height: 117%;
  color: #FFA800;
}

.choice_seat_start {
  font-size: 16px;
  font-weight: 400;
  line-height: 117%;
  color: #928F94;
}

.price {
  font-family: 'PT Sans', serif;
  color: #928F94;
  min-width: 16px;
  min-height: 19px;
}

.choice_price {
  font-size: 24px;
  font-weight: 700;
  line-height: 116%;
  color: #2D2B2F;
  text-decoration: none;
  margin: 0px 4px 0px 0px;
}

.choice_price_group {
  margin: 41px 22px 0px 24px;
}

.choice_seat {
  margin: 0px 17px 0px 0px;
}

.choice_seat_quantity {
  margin: 0px 17px 0px 0px;
  text-decoration: none;
}

.choice_seat_start {
  margin: 0px 15px 0px 0px;
}

.choice_button {
  background: #FFA800;
  border-radius: 5px;
  width: 228px;
  height: 39px;
  margin: 0px 10px 24px 34px;
}

.button {
  text-decoration: none;
  color: #FFFFFF;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  line-height: 116%;
  font-weight: 700;
}

.choice_price_group_select {
  background: #E5E5E5;
  border-radius: 5px;
  margin: 8px 4px 26px 3px;
}

.button:active {
  background: #E5E5E5;
  color: #FFA800;
}

/*---------------------------------------------part 2-----------------------------*/
.choice_two {
  width: 959px;
  height: 268px;
  background: #FFFFFF;
  border: 1px solid #C4C4C4;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
  display: flex;
  margin: 0 auto;
  margin-bottom: 47px;
}

.choice_column_a_train_container_two {
  display: flex;
  flex-direction: column;
  background: #E4E0E9;
  width: 196px;
  height: 266px;
}

.choice_column_a_train_two {
  margin: 32px auto 31px auto;
  align-items: center;
}

.choice_icon_services {
  margin: 29px 28px 33px 168.7px;
}
